* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #9CA986;
    color: white;
}

.container {
    margin: 1rem;
    text-align: center;
}

.nav {
    background-color: #5F6F65;
    color: rgb(255, 255, 255);
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
}

.site-title {
    font-size: 2.5rem;
    border: 8px solid #808D7C;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
    font-size: 1.5rem;
    border: 8px solid #808D7C;
}

.nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}

.nav li.active {
    background-color: #808D7C;
}

.nav li:hover {
    background-color: #808D7C;
}

